*{
    margin: 0;
    box-sizing: border-box;
}
div.wrapper{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 92%;
    margin: 0 auto;
}

nav a {
    text-decoration: none;
    padding: 0 15px;
    color: black;
}
div.logo img{
    width:150px;
}
div.set a{
    text-decoration: none;
    color: black;
}
div.banner{
    display: flex;
    justify-content: space-between;
    width: 92%;
    margin: 0 auto; 
}
div.wrapper2{
    background-image: url(../Image/second.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: darken;
    padding: 90px 0;
}
div.box1 h1{
    font-size: 55px;
    font-weight: 400;
}
div.box1{
    color: #fff;
}
div.box2{
    background-color: #fff;
    border-radius: 10px;
    width: 30%;
}
div.side{
    width: 90%;
    margin: 20px auto;
}
div.side p{
    margin: 5px 0;
}
form input{
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #a2a0a0;
    width: 100%;
    border-radius: 5px;
}
form button{
    border: none;
    width: 100%;
    margin: 10px 0;
    padding: 10px;
    border-radius: 5px;
    background-color: #078586;
    color: #fff;
}
div.partner{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 70%;
  margin: 0 auto;
  
}
div.wrapper3{
    padding: 50px 0;
    background-color: #f5fefe;
}
div.wrapper3 p{
    margin: 25px 0;
}
div.partner img{
    width: 9%;
}
div.wrapper3 p{
    text-align: center;
    color: #4F4D55;
}
div.wrapper4{
    padding: 50px 0;
}
div.firstRow h4,div.firstRow p{
    color: #4F4D55;
}
div.firstRow p{
    margin: 10px 0;
}
div.wrapper4 div.firstRow {
    text-align: center;
}
div.wrapper4 div.secondRow{
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    width: 80%;
    margin: 20px auto;
}
div.innerBox1 p,div.innerBox2 p,div.innerBox3 p,div.innerBox4 p,div.innerBox5 p,div.innerBox6 p{
    border-top: 1px solid rgba(255, 255, 255,0.3);
    ;
}
div.innerBox1{
    background-image: url(../Image/coode.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
   color: #fff;
   border-radius: 10px;
   width: 30%;
    height: 40vb;
}
div.innerBox2{
    background-image: url(../Image/maan.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: #fff;
    border-radius: 10px;
    width: 30%;
    height: 40vb;
}
div.innerBox3{
    background-image: url(../Image/paperHand.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    border-radius: 10px;
    width: 30%;
    height: 40vb;
}
div.innerBox4{
    background-image: url(../Image/compp.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    border-radius: 10px;
    width: 30%;
    height: 40vb;
}
div.innerBox5{
    background-image: url(../Image/some.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: #fff;
    border-radius: 10px;
    width: 30%;
    height: 40vb;
}
div.innerBox6{
    background-image: url(../Image/taag.jpeg);
    background-size: cover;
    background-position: center;
    color: #fff;
    border-radius: 10px;
    width: 30%;
    height: 40vh;
}
div.innerBox6 h3{
    text-align: center;
}
div.three img {
    width:16rem;
}
div.second{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 60px 0;
}
section.wrapper5{
    background-color: #078586;
    color: #fff;
    padding: 20px 0;
 
}
div.two,div.five{
    margin: 100px 0;
}
div.first{
    text-align: center;
}
div.first h2{
    margin: 10px 0;
}
div.innerwrap{
    width: 90%;
    margin: 0 auto;
}
div.question{
    text-align: center;
    background-color:#F7F7F4;
    width: 60%;
    margin: 0 auto;
    padding: 35px 0;
}
div.question p{
    margin: 10px 0;
}
div.question button{
    padding: 10px;
    border-radius: 5px;
    background-color:  #078586 ;
    border: none;
    color: #fff;
}
footer{
    background-color: #078586;
}
div.footerwrap{
    text-align: center;
    padding: 40px 0;
}
div.secondfooterwrap{
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
    color: #fff;
    border-top: 1px solid #fff;
    padding: 20px 0;
}
div.secondfooterwrap a{
    color: #fff;
    text-decoration: none;
    padding: 0 25px;
}
div.footerImage img{
    width: 200px;
}

.faq-item {
    margin-bottom: 20px;
  }
  
  
  .faq-answer {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  div.que{
    margin: 0 auto;
    width: 50%;
  }
  div.tagQuest{
    text-align: center;
  }
  div.tagQuest p{
    width: 15%;
    margin: 0 auto;
    padding: 5px;
    color: #089FA0;
    background-color: #E7FEFE;
  }
  div.tagQuest h1{
    font-size: 50px;
    font-weight: 500;
  }
      div.secondRow div{
        display: flex;
        flex-direction: column;
        justify-content: end;
      }
      div.secondRow div p{
        margin: 15px;
      }
      div.footerwrap i{
        color: #fff;
        margin: 0 10px;
      }
      div.quest{
        margin: 40px 0;
      }
      div.all{
        width: 55%;
        margin: 0 auto;
      }
      div.onee{
        border: 1px solid #888;
        padding: 10px 10px;
        margin: 10px 0;
        border-radius: 6px;
      }
      div.onee a{
        text-decoration: none;
        color: #078586;
      }
      div.drop{
        display: flex;
        justify-content: space-between;
      }
      @media (max-width : 1200px) {
        div.banner{
            display: block;
        }
        div.box2{
            background-color: #fff;
            border-radius: 10px;
            width: 100%;
        }
        div.side{
            width: 90%;
            margin: 50px auto;
            padding: 20px 0;
        }
        div.wrapper4{
            border: 1px solid black;
        }
        div.wrapper4 div.secondRow{
          display: block;
          width: 100%;
          margin:0 auto;
          
        }
       
        div.innerBox1,div.innerBox2,div.innerBox3,div.innerBox4,div.innerBox5,div.innerBox6{
            width: 90%;
            margin: 30px auto;
        }
        div.wrapper4 p{
           display: none;
        }
        div.all{
            width: 90%;
            margin: 0 auto;
          }
          div.question{
            width: 90%;
            margin: 0 auto;
        }
        div.que{
            margin: 0 auto;
            width: 100%;
          }
          div.tagQuest h1{
            font-size: 40px;
            font-weight: 500;
          }
        div.secondfooterwrap a{
            color: #fff;
            text-decoration: none;
            padding: 0 8px;
        }
        div.second{
            display: block;
            text-align: center;
        }
        div.one h3,div.four h3{
            font-size: 30px;
        }
        div.one p,div.four p{
            font-size: 20px;
        }
        div.three img{
          width: 400px;
          margin: 10px 0;
        }
      }
     